/** Media Queries **/
.contenedor-nueva-tarea {
  display: flex;
  justify-content: center;
}
.contenedor-nueva-tarea .agregar-tarea {
  background-color: #2563EB;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .contenedor-nueva-tarea .agregar-tarea {
    width: auto;
  }
}
.contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #134cca;
  cursor: pointer;
}

.listado-tareas {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
}
.listado-tareas .no-tareas {
  text-align: center;
}

.tarea {
  padding: 0.5rem 0.2rem;
  border-bottom: 2px solid #e4e7eb;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tarea:last-of-type {
  border: none;
}
.tarea p {
  font-size: 1.4rem;
}
.tarea .opciones {
  display: flex;
  gap: 1rem;
}
.tarea .opciones button {
  border: none;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 1rem;
}
.tarea .opciones button:hover {
  cursor: pointer;
}
.tarea .opciones .completado {
  background-color: #2563EB;
  color: #FFFFFF;
}
.tarea .opciones .pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.tarea .opciones .eliminar-tarea {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
}