@use '../base/variable' as v;
@use '../base/mixins' as m;

.contenedor-nueva-tarea{

    display: flex;
    justify-content: center;
    .agregar-tarea{
        @include m.boton(v.$azul, v.$blanco);
        border-radius: .5rem;
        
    }
}

.listado-tareas{
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    .no-tareas{
        text-align: center;
        
    }

}

.tarea{
padding: .5rem .2rem;
border-bottom: 2px solid darken(v.$grisClaro, 5%);
margin-bottom: 1rem;
display: flex;
justify-content: space-between;
align-items: center;

&:last-of-type{
    border: none;
}

p{
    font-size: 1.4rem;
}

.opciones{

    display: flex;
    gap: 1rem;

    button{
        border: none;
        font-size: .9rem;
        padding: .5rem 1rem;
        font-weight: 700;
        text-transform: uppercase;
        border-radius: 1rem;

        &:hover{
            cursor: pointer;
        }
    }

    .completado{
        background-color: v.$azul;
        color: v.$blanco;
    }

    .pendiente{
        background-color: v.$naranja;
        color: v.$blanco;
    }

    .eliminar-tarea {
        background-color: v.$rojo;
        color: v.$blanco;
    }
}


}